import React, { Component } from "react";
import { createNumberMask } from "redux-form-input-masks";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { connect } from "react-redux";
import Axios from "axios";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

const server = process.env.REACT_APP_BACKEND_URL;
class AccordTransferMurni extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listBank: [],
    };
  }
  onChange() {
    this.props.change("transferNetto", this.props.transferNettoMurni);
  }
  getlocal() {
    var transfer = 0,
      harga = 0,
      netto = 0,
      tujuan = "",
      supplier = "",
      tanggal = "";
    var local = JSON.parse(localStorage.getItem("alatBayarTrfRp"))
      ? JSON.parse(localStorage.getItem("alatBayarTrfRp"))
      : "kosong";
    if (local === "kosong") {
      transfer = 0;
      harga = 0;
      netto = 0;
      tujuan = "";
      supplier = "";
      tanggal = "";
    } else {
      transfer = local[0].transferRp;
      harga = local[0].transferKurs;
      netto = local[0].transferKurs;
      tujuan = local[0].accTo;
      supplier = local[0].accFrom;
      tanggal = local[0].transferTanggalJanji;
    }
    this.props.change("transfer", transfer);
    this.props.change("harga", harga);
    this.props.change("transferNetto", netto);
    this.props.change("tanggal", tanggal);
    this.props.change("tujuan", tujuan);
    this.props.change("supplier", supplier);
  }

  componentDidMount() {
    Axios.get(server + "banks")
      .then((res) =>
        this.setState({
          listBank: res.data,
        })
      )
      .catch((err) => console.log(err))
      .then(() => this.getlocal());
  }
  render() {
    return (
      <div className="col-lg-6 mt-3">
        <div id="accordion" className="custom-accordion mb-4">
          <div className="card mb-0 bg-dark">
            <div className="card-header" id="headingOne">
              <div className="row">
                <div className="col-lg-8 col-md-8">
                  <h5 className="m-0">
                    <a
                      className="custom-accordion-title d-block pt-2 pb-2 text-white"
                      data-toggle="collapse"
                      href="#collapseTransfer"
                      aria-expanded="true"
                      aria-controls="collapseTransfer"
                    >
                      Transfer
                      <span className="float-right">
                        <i className="mdi mdi-chevron-down accordion-arrow" />
                      </span>
                    </a>
                  </h5>
                </div>
                <div className="col-md-4 col-lg-4">
                  <label htmlFor="" style={{ color: "white" }}>
                    Netto
                  </label>
                  <Field
                    name="transferNetto"
                    type="telp"
                    component="input"
                    className="form-control"
                    readOnly={true}
                    {...currencyMask}
                  />
                </div>
              </div>
            </div>
            <div
              id="collapseTransfer"
              className="collapse"
              aria-labelledby="headingOne"
              data-parent="#accordion"
            >
              <div className="card-body bg-white">
                <form onSubmit={this.props.handleSubmit}>
                  <div className="row">
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Transfer</label>
                      <Field
                        name="transfer"
                        component="input"
                        type="tel"
                        className="form-control"
                        onBlur={this.onChange()}
                        {...currencyMask}
                      />
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Harga </label>
                      <div className="form-gorup">
                        <Field
                          name="harga"
                          component="input"
                          type="tel"
                          className="form-control"
                          onBlur={this.onChange()}
                          readOnly={true}
                          {...currencyMask}
                        />
                      </div>
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Netto </label>
                      <Field
                        name="transferNetto"
                        label="Netto"
                        type="tel"
                        className="form-control"
                        component="input"
                        readOnly={true}
                        {...currencyMask}
                      />
                    </div>
                    <div className="col-md-6 col-lg-6">
                      <Field
                        name="tujuan"
                        label="A/C Tujuan"
                        type="text"
                        component={renderField}
                      />
                    </div>
                    <div className="col-md-6 col-lg-6">
                      <label htmlFor="">A/C Asal</label>
                      <Field
                        name="kodeBank"
                        className="form-control"
                        component="select"
                      >
                        <option value="DEFAULT" key="DEFAULT">
                          silahkan pilih
                        </option>
                        {this.state.listBank.map((list) => (
                          <option value={list.noRekening} key={list.noRekening}>
                            {list.atasNama}
                          </option>
                        ))}
                      </Field>
                    </div>
                    <div className="col-md-6 col-lg-6">
                      <Field
                        name="tanggal"
                        label="Tanggal"
                        type="date"
                        component={renderField}
                      />
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button className="btn btn-primary" type="submit">
                          Simpan
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
          {/* end card*/}
        </div>
      </div>
    );
  }
}

AccordTransferMurni = reduxForm({
  form: "AccordTransferMurni",
  enableReinitialize: true,
})(AccordTransferMurni);
const selector = formValueSelector("AccordTransferMurni"); // <-- same as form name
export default connect((state) => {
  const { harga, transfer } = selector(state, "harga", "transfer");
  return {
    transferNetto: (parseFloat(transfer) / parseFloat(harga)).toFixed(3),
    transferNettoMurni: parseFloat(transfer),
  };
})(AccordTransferMurni);
